import { Component } from '@angular/core';

import DataService from './data.service';
import getTimeLabel from './get-time-labal';

@Component({
	selector: 'wp-counter',
	standalone: true,
	templateUrl: './counter.component.html',
	styleUrls: ['./counter.component.scss']
})
export default class CounterComponent {
	timeLabel: string = '';

	constructor(
		private readonly _dataService: DataService
	) { }

	ngOnInit(): void {
		this.timeLabel = this.getTimeLabel();
		setInterval(
			() => {
				this.timeLabel = this.getTimeLabel();
			},
			1000
		);
	}

	private getTimeLabel(): string {
		const timeRemaining_ms = this._dataService.getTimeRemaining_ms();
		return getTimeLabel(timeRemaining_ms);
	}
}
