import { Injectable } from '@angular/core';

const END_TIME = new Date(
	2023,
	0,
	30,
	18,
	30
);

@Injectable({providedIn: 'root'})
export default class DataService {
	getTimeRemaining_ms(): number {
		const now = Date.now();
		const res = END_TIME.getTime() - now;
		if (res < 0) {
			return 0;
		}
		return res;
	}
}
